import css from '../style/css';
import unit from '../style/unit';
import tf from '../style/transform';
import abbr from '../style/abbr';
import enums from '../util/enums';
import util from '../util/util';
import Event from '../util/Event';
import inject from '../util/inject';
import frame from './frame';
import easing from './easing';
import change from '../refresh/change';
import key from './key';
import mx from '../math/matrix';

const {
  STYLE_KEY: {
    FILTER,
    TRANSFORM_ORIGIN,
    PERSPECTIVE_ORIGIN,
    BACKGROUND_CLIP,
    BACKGROUND_POSITION_X,
    BACKGROUND_POSITION_Y,
    BOX_SHADOW,
    TRANSLATE_X,
    TRANSLATE_Y,
    TRANSLATE_Z,
    BACKGROUND_SIZE,
    FONT_SIZE,
    FLEX_BASIS,
    FLEX_DIRECTION,
    WIDTH,
    HEIGHT,
    TOP,
    BOTTOM,
    LINE_HEIGHT,
    OPACITY,
    Z_INDEX,
    TRANSFORM,
    COLOR,
    FONT_WEIGHT,
    FONT_STYLE,
    FONT_FAMILY,
    TEXT_ALIGN,
    MATRIX,
    ROTATE_3D,
    TRANSLATE_PATH,
    TEXT_STROKE_COLOR,
    TEXT_STROKE_OVER,
    STROKE_WIDTH,
  },
} = enums;
const { AUTO, PX, PERCENT, INHERIT, RGBA, STRING, NUMBER, REM, VW, VH, VMAX, VMIN, GRADIENT, calUnit } = unit;
const { isNil, isFunction, isNumber, isObject, clone, equalArr } = util;
const { linear } = easing;
const { cloneStyle } = css;
const { isGeom, GEOM } = change;

const {
  COLOR_HASH,
  LENGTH_HASH,
  RADIUS_HASH,
  GRADIENT_HASH,
  EXPAND_HASH,
  GRADIENT_TYPE,
} = key;

function unify(frames, target) {
  let hash = {};
  let keys = [];
  // 获取所有关键帧的属性
  frames.forEach(item => {
    let style = item.style;
    Object.keys(style).forEach(k => {
      let v = style[k];
      // 未定义的过滤掉，null空有意义
      if(v !== undefined && !hash.hasOwnProperty(k)) {
        hash[k] = true;
        // geom为属性字符串，style都为枚举int
        if(!GEOM.hasOwnProperty(k)) {
          k = parseInt(k);
        }
        // path动画要转为translateXY，所以手动添加，使2帧之间存在过渡，有可能之前已存在这个动画，可忽视
        if(k === TRANSLATE_PATH) {
          if(!hash.hasOwnProperty(TRANSLATE_X)) {
            keys.push(TRANSLATE_X);
          }
          if(!hash.hasOwnProperty(TRANSLATE_Y)) {
            keys.push(TRANSLATE_Y);
          }
          hash[TRANSLATE_X] = hash[TRANSLATE_Y] = true;
        }
        keys.push(k);
      }
    });
  });
  // 添补没有声明完全的关键帧属性为节点当前值
  frames.forEach(item => {
    let style = item.style;
    keys.forEach(k => {
      if(!style.hasOwnProperty(k) || isNil(style[k])) {
        if(GEOM.hasOwnProperty(k)) {
          style[k] = target.getProps(k);
        }
        else {
          if(k === TRANSLATE_X && style.hasOwnProperty(TRANSLATE_PATH)) {
            style[k] = style[TRANSLATE_PATH][0];
          }
          else if(k === TRANSLATE_Y && style.hasOwnProperty(TRANSLATE_PATH)) {
            style[k] = style[TRANSLATE_PATH][1];
          }
          else {
            style[k] = target.currentStyle[k];
          }
        }
      }
    });
  });
  return keys;
}

// 每次初始化时处理继承值，以及转换transform为单matrix矩阵
function inherit(frames, keys, target) {
  let computedStyle = target.computedStyle;
  frames.forEach(item => {
    let style = item.style;
    keys.forEach(k => {
      let v = style[k];
      // geom的属性可能在帧中没有
      if(isNil(v)) {
        return;
      }
      if(k === TRANSFORM) {
        let ow = target.outerWidth;
        let oh = target.outerHeight;
        let m = tf.calMatrix(v, ow, oh);
        style[k] = [{ k: MATRIX, v: m }];
      }
      else if(v.u === INHERIT) {
        if(k === COLOR || k === TEXT_STROKE_COLOR) {
          style[k] = { v: util.rgba2int(computedStyle[k]), u: RGBA };
        }
        else if(LENGTH_HASH.hasOwnProperty(k)) {
          style[k] = { v: computedStyle[k], u: PX };
        }
        else if(k === FONT_WEIGHT) {
          style[k] = { v: computedStyle[k], u: NUMBER };
        }
        else if(k === FONT_STYLE || k === FONT_FAMILY || k === TEXT_ALIGN || k === TEXT_STROKE_OVER) {
          style[k] = { v: computedStyle[k], u: STRING };
        }
      }
    });
  });
}

/**
 * 通知root更新当前动画，需要根据frame的状态来决定是否是同步插入
 * 在异步时，因为动画本身是异步，需要addRefreshTask
 * 而如果此时frame在执行before过程中，说明帧动画本身是在before计算的，需要同步插入
 * @param animation
 * @param style
 * @param keys 样式所有的key
 * @param root
 * @param node
 */
function genBeforeRefresh(animation, style, keys, root, node) {
  let res = {
    node,
    style,
    keys,
  };
  root.__addUpdate(node, root, res);
  animation.__style = style;
  animation.__assigning = true;
  // frame每帧回调时，下方先执行计算好变更的样式，这里特殊插入一个hook，让root增加一个刷新操作
  // 多个动画调用因为相同root也只会插入一个，这样在所有动画执行完毕后frame里检查同步进行刷新，解决单异步问题
  root.__frameHook();
}

/**
 * 将每帧的样式格式化，提取出offset属性并转化为时间，提取出缓动曲线easing
 * @param style 关键帧样式
 * @param duration 动画时间长度
 * @param es options的easing曲线控制，frame没有自定义则使用全局的
 * @returns {{style: *, time: number, easing: *, transition: []}}
 */
function framing(style, duration, es) {
  let { offset, easing } = style;
  // 这两个特殊值提出来存储不干扰style
  delete style.offset;
  delete style.easing;
  // translatePath特殊对待，ae的曲线运动动画
  let translatePath = style.translatePath;
  style = css.normalize(style);
  if(Array.isArray(translatePath) && [6, 8].indexOf(translatePath.length) > -1) {
    style[TRANSLATE_PATH] = translatePath.map(item => {
      let v = calUnit(item);
      if(v.u === NUMBER) {
        v.u = PX;
      }
      return v;
    });
  }
  return {
    style,
    time: offset * duration,
    easing: easing || es,
    timingFunction: getEasing(easing || es),
    transition: [],
  };
}

function calByUnit(p, n, container, root) {
  if(p.u === PX) {
    if(n.u === PERCENT) {
      return n.v * 0.01 * container - p.v;
    }
    else if(n.u === REM) {
      return n.v * root.computedStyle[FONT_SIZE] - p.v;
    }
    else if(n.u === VW) {
      return n.v * root.width * 0.01 - p.v;
    }
    else if(n.u === VH) {
      return n.v * root.height * 0.01 - p.v;
    }
    else if(n.u === VMAX) {
      return n.v * Math.max(root.width, root.height) * 0.01 - p.v;
    }
    else if(n.u === VMIN) {
      return n.v * Math.min(root.width, root.height) * 0.01 - p.v;
    }
  }
  else if(p.u === PERCENT) {
    if(n.u === PX) {
      return n.v * 100 / container - p.v;
    }
    else if(n.u === REM) {
      return n.v * root.computedStyle[FONT_SIZE] * 100 / container - p.v;
    }
    else if(n.u === VW) {
      return n.v * root.width / container - p.v;
    }
    else if(n.u === VH) {
      return n.v * root.height / container - p.v;
    }
    else if(n.u === VMAX) {
      return n.v * Math.max(root.width, root.height) / container - p.v;
    }
    else if(n.u === VMIN) {
      return n.v * Math.min(root.width, root.height) / container - p.v;
    }
  }
  else if(p.u === REM) {
    if(n.u === PX) {
      return n.v / root.computedStyle[FONT_SIZE] - p.v;
    }
    else if(n.u === PERCENT) {
      return n.v * 0.01 * container / root.computedStyle[FONT_SIZE] - p.v;
    }
    else if(n.u === VW) {
      return n.v * root.width * 0.01 / root.computedStyle[FONT_SIZE] - p.v;
    }
    else if(n.u === VH) {
      return n.v * root.height * 0.01 / root.computedStyle[FONT_SIZE] - p.v;
    }
    else if(n.u === VMAX) {
      return n.v * Math.max(root.width, root.height) * 0.01 / root.computedStyle[FONT_SIZE] - p.v;
    }
    else if(n.u === VMIN) {
      return n.v * Math.min(root.width, root.height) * 0.01 / root.computedStyle[FONT_SIZE] - p.v;
    }
  }
  else if(p.u === VW) {
    if(n.u === PX) {
      return n.v * 100 / root.width - p.v;
    }
    else if(n.u === REM) {
      return n.v * 100 * root.computedStyle[FONT_SIZE] / root.width - p.v;
    }
    else if(n.u === PERCENT) {
      return n.v * container / root.width - p.v;
    }
    else if(n.u === VH) {
      return n.v * root.height / root.width - p.v;
    }
    else if(n.u === VMAX) {
      return n.v * Math.max(root.width, root.height) / root.width - p.v;
    }
    else if(n.u === VMIN) {
      return n.v * Math.min(root.width, root.height) / root.width - p.v;
    }
  }
  else if(p.u === VH) {
    if(n.u === PX) {
      return n.v * 100 / root.height - p.v;
    }
    else if(n.u === REM) {
      return n.v * 100 * root.computedStyle[FONT_SIZE] / root.height - p.v;
    }
    else if(n.u === VW) {
      return n.v * root.width / root.height - p.v;
    }
    else if(n.u === PERCENT) {
      return n.v * container / root.height - p.v;
    }
    else if(n.u === VMAX) {
      return n.v * Math.max(root.width, root.height) / root.height - p.v;
    }
    else if(n.u === VMIN) {
      return n.v * Math.min(root.width, root.height) / root.height - p.v;
    }
  }
  else if(p.u === VMAX) {
    if(n.u === PX) {
      return n.v * 100 / Math.max(root.width, root.height) - p.v;
    }
    else if(n.u === REM) {
      return n.v * 100 * root.computedStyle[FONT_SIZE] / Math.max(root.width, root.height) - p.v;
    }
    else if(n.u === PERCENT) {
      return n.v * container / Math.max(root.width, root.height) - p.v;
    }
    else if(n.u === VW) {
      return n.v * root.width / Math.max(root.width, root.height) - p.v;
    }
    else if(n.u === VH) {
      return n.v * root.height / Math.max(root.width, root.height) - p.v;
    }
    else if(n.u === VMIN) {
      return n.v * Math.min(root.width, root.height) / Math.max(root.width, root.height) - p.v;
    }
  }
  else if(p.u === VMIN) {
    if(n.u === PX) {
      return n.v * 100 / Math.min(root.width, root.height) - p.v;
    }
    else if(n.u === REM) {
      return n.v * 100 * root.computedStyle[FONT_SIZE] / Math.min(root.width, root.height) - p.v;
    }
    else if(n.u === PERCENT) {
      return n.v * container / Math.min(root.width, root.height) - p.v;
    }
    else if(n.u === VW) {
      return n.v * root.width / Math.min(root.width, root.height) - p.v;
    }
    else if(n.u === VH) {
      return n.v * root.height / Math.min(root.width, root.height) - p.v;
    }
    else if(n.u === VMAX) {
      return n.v * Math.max(root.width, root.height) / Math.min(root.width, root.height) - p.v;
    }
  }
  return 0;
}

/**
 * 计算两帧之间的差，单位不同的以后面为准，返回的v表示差值
 * 没有变化返回空
 * auto等无法比较的不参与计算
 * @param prev 上一帧样式
 * @param next 下一帧样式
 * @param k 比较的样式名
 * @param target dom对象
 * @returns {{k: *, v: *}}
 */
function calDiff(prev, next, k, target) {
  let res = {k};
  let p = prev[k];
  let n = next[k];
  if(k === TRANSFORM) {
    // transform不存在时需给默认矩阵，他只有1个matrix3d的值做动画
    if(!p && !n || !p.length && !n.length) {
      return;
    }
    let pm, nm;
    if(p && p[0]) {
      pm = p[0].v;
    }
    else {
      pm = mx.identity();
    }
    if(n && n[0]) {
      nm = n[0].v;
    }
    else {
      nm = mx.identity();
    }
    // transform特殊被初始化转成matrix矩阵，直接计算差值
    if(equalArr(pm, nm)) {
      return;
    }
    res.v = [
      nm[0] - pm[0],
      nm[1] - pm[1],
      nm[2] - pm[2],
      nm[3] - pm[3],
      nm[4] - pm[4],
      nm[5] - pm[5],
      nm[6] - pm[6],
      nm[7] - pm[7],
      nm[8] - pm[8],
      nm[9] - pm[9],
      nm[10] - pm[10],
      nm[11] - pm[11],
      nm[12] - pm[12],
      nm[13] - pm[13],
      nm[14] - pm[14],
      nm[15] - pm[15],
    ];
  }
  else if(k === ROTATE_3D) {
    if(p[0] === n[0] && p[1] === n[1] && p[2] === n[2]
      && p[3].v === n[3].v && p[3].u === n[3].u) {
      return;
    }
    res.v = [n[0] - p[0], n[1] - p[1], n[2] - p[2], n[3].v - p[3].v];
  }
  else if(k === FILTER) {
    // filter很特殊，里面有多个滤镜，按顺序计算，为空视为默认值，如blur默认0，brightness默认1
    let len = Math.max(p ? p.length : 0, n ? n.length : 0);
    let v = [];
    for(let i = 0; i < len; i++) {
      let pv = p ? p[i] : null, nv = n ? n[i] : null;
      // 空或key不等都无变化
      if(isNil(pv) || isNil(nv) || pv.k !== nv.k) {
        v.push(null);
      }
      else {
        let k = pv.k, pvv = pv.v, nvv = nv.v;
        if(k === 'blur') {
          if(pvv.u === nvv.u) {
            v.push(nvv.v - pvv.v);
          }
          else {
            let v2 = calByUnit(pvv, nvv, 0, target.root);
            v.push(v2);
          }
        }
        else if(k === 'hueRotate' || k === 'saturate' || k === 'brightness' || k === 'contrast'
          || k === 'sepia' || k === 'invert' || k === 'grayscale') {
          v.push(nvv.v - pvv.v);
        }
        else if(k === 'dropShadow') {
          let v2 = [];
          for(let i = 0; i < 4; i++) {
            let a = pvv[i], b = nvv[i];
            if(a.u === b.u) {
              v2.push(b.v - a.v);
            }
            else {
              v2.push(calByUnit(a, b, i === 1 ? target.clientHeight: target.clientWidth, target.root));
            }
          }
          v2.push([
            nvv[4][0] - pvv[4][0],
            nvv[4][1] - pvv[4][1],
            nvv[4][2] - pvv[4][2],
            nvv[4][3] - pvv[4][3],
          ])
          v.push(v2);
        }
      }
    }
    res.v = v;
  }
  else if(k === TRANSFORM_ORIGIN || k === PERSPECTIVE_ORIGIN || RADIUS_HASH.hasOwnProperty(k)) {
    // x/y都相等无需
    if(n[0].v === p[0].v && n[0].u === p[0].u
      && n[1].v === p[1].v && n[1].u === p[1].u) {
      return;
    }
    res.v = [];
    for(let i = 0; i < 2; i++) {
      let pi = p[i];
      let ni = n[i];
      if(pi.u === ni.u) {
        res.v.push(ni.v - pi.v);
      }
      else {
        let v = calByUnit(pi, ni, target[i ? 'outerHeight' : 'outerWidth'], target.root);
        res.v.push(v);
      }
    }
  }
  else if(k === BACKGROUND_POSITION_X || k === BACKGROUND_POSITION_Y) {
    res.v = [];
    let length = Math.min(p.length, n.length);
    for(let i = 0; i < length; i++) {
      let pi = p[i], ni = n[i];
      if(pi.u === ni.u) {
        let v = ni.v - pi.v;
        res.v.push(v);
      }
      else {
        let k2;
        if(['padding-box', 'paddingBox'].indexOf(target.computedStyle[BACKGROUND_CLIP]) > -1) {
          k2 = k === BACKGROUND_POSITION_X ? 'clientWidth' : 'clientHeight';
        }
        else if(['content-box', 'contentBox'].indexOf(target.computedStyle[BACKGROUND_CLIP]) > -1) {
          k2 = k === BACKGROUND_POSITION_X ? 'width' : 'height';
        }
        else {
          k2 = k === BACKGROUND_POSITION_X ? 'offsetWidth' : 'offsetHeight';
        }
        let v = calByUnit(pi, ni, target[k2], target.root);
        res.v.push(v);
      }
    }
  }
  else if(k === BOX_SHADOW) {
    res.v = [];
    for(let i = 0, len = Math.min(p.length, n.length); i < len; i++) {
      let a = p[i];
      let b = n[i];
      // 不能为空，outset/inset必须相等
      if(!a || !b || a[5] !== b[5]) {
        res.v.push(null);
        continue;
      }
      let v = [];
      // x/y/blur/spread
      for(let j = 0; j < 4; j++) {
        if(a[j].u === b[j].u) {
          v.push(b[j].v - a[j].v);
        }
        else {
          let v2 = calByUnit(a[j], b[j], i === 1 ? target.offsetHeight : target.offsetWidth, target.root);
          v.push(v2);
        }
      }
      // rgba
      let c = [];
      for(let j = 0; j < 4; j++) {
        c.push(b[4][j] - a[4][j]);
      }
      v.push(c);
      res.v.push(v);
    }
  }
  else if(k === BACKGROUND_SIZE) {
    res.v = [];
    let length = Math.min(p.length, n.length);
    let has;
    for(let i = 0; i < length; i++) {
      let pi = p[i], ni = n[i];
      if(!pi || !ni) {
        res.v.push(null);
        continue;
      }
      let temp = [];
      for(let j = 0; j < 2; j++) {
        let pp = pi[j], nn = ni[j];
        if(pp.u === nn.u) {
          temp.push(nn.v - pp.v);
        }
        else {
          let k2;
          if(['padding-box', 'paddingBox'].indexOf(target.computedStyle[BACKGROUND_CLIP]) > -1) {
            k2 = i ? 'clientWidth' : 'clientHeight';
          }
          else if(['content-box', 'contentBox'].indexOf(target.computedStyle[BACKGROUND_CLIP]) > -1) {
            k2 = i ? 'width' : 'height';
          }
          else {
            k2 = i ? 'offsetWidth' : 'offsetHeight';
          }
          let v = calByUnit(pp, nn, target[k2], target.root);
          temp.push(v);
        }
      }
      if(equalArr(temp, [0, 0])) {
        res.v.push(null);
      }
      else {
        res.v.push(temp);
        has = true;
      }
    }
    if(!has) {
      return;
    }
  }
  else if(k === OPACITY || k === Z_INDEX) {
    if(n === p) {
      return;
    }
    res.v = n - p;
  }
  else if(k === STROKE_WIDTH) {
    res.v = [];
    let length = Math.min(p.length, n.length);
    for(let i = 0; i < length; i++) {
      let pi = p[i], ni = n[i];
      if(pi.u === ni.u) {
        let v = ni.v - pi.v;
        res.v.push(v);
      }
      let v = calByUnit(pi, ni, target.offsetWidth, target.root);
      res.v.push(v);
    }
  }
  // 特殊的path，不存在style中但在动画某帧中，不会统一化所以可能反向计算frameR时后一帧没有
  else if(k === TRANSLATE_PATH && p) {
    let k1 = 'offsetWidth', k2 = 'offsetHeight';
    if(['padding-box', 'paddingBox'].indexOf(target.computedStyle[BACKGROUND_CLIP]) > -1) {
      k1 = 'clientWidth';
      k2 = 'clientHeight';
    }
    else if(['content-box', 'contentBox'].indexOf(target.computedStyle[BACKGROUND_CLIP]) > -1) {
      k1 = 'width';
      k2 = 'height';
    }
    res.v = p.map((item, i) => {
      let { v, u } = item;
      if(u === PERCENT) {
        if(i % 2 === 0) {
          return { v: (parseFloat(v) || 0) * 0.01 * target[k1], u: PX };
        }
        else {
          return { v: (parseFloat(v) || 0) * 0.01 * target[k2], u: PX };
        }
      }
      else if(u === REM) {
        return { v: (parseFloat(v) || 0) * target.root.computedStyle[FONT_SIZE] * 100, u: PX };
      }
      else if(u === VW) {
        return { v: (parseFloat(v) || 0) * 0.01 * target.root.width, u: PX };
      }
      else if(u === VH) {
        return { v: (parseFloat(v) || 0) * 0.01 * target.root.height, u: PX };
      }
      else if(u === VMAX) {
        return { v: (parseFloat(v) || 0) * 0.01 * Math.max(target.root.width, target.root.height), u: PX };
      }
      else if(u === VMIN) {
        return { v: (parseFloat(v) || 0) * 0.01 * Math.min(target.root.width, target.root.height), u: PX };
      }
      else {
        return { v: parseFloat(v) || 0, u: PX };
      }
    });
  }
  else if(EXPAND_HASH.hasOwnProperty(k)) {
    if(p.u === n.u) {
      let v = n.v - p.v;
      if(v === 0) {
        return;
      }
      res.v = v;
    }
    else {
      let v = calByUnit(p, n, target[k === TRANSLATE_X || k === TRANSLATE_Z ? 'outerWidth' : 'outerHeight'], target.root);
      if(!v) {
        return;
      }
      res.v = v;
    }
  }
  else if(LENGTH_HASH.hasOwnProperty(k)) {
    // auto不做动画
    if(p.u === AUTO || n.u === AUTO) {
      return;
    }
    let computedStyle = target.computedStyle;
    let parentComputedStyle = (target.domParent || target).computedStyle;
    let diff = 0;
    if(p.u === n.u) {
      diff = n.v - p.v;
    }
    // lineHeight奇怪的单位变化，%相对于fontSize
    else if(k === LINE_HEIGHT) {
      diff = calByUnit(p, n, computedStyle[FONT_SIZE], target.root);
    }
    // fontSize的%相对于parent的
    else if(k === FONT_SIZE) {
      diff = calByUnit(p, n, parentComputedStyle[FONT_SIZE], target.root);
    }
    // 相对于父height的特殊属性
    else if(k === FLEX_BASIS
      && ['column', 'column-reverse', 'columnReverse'].indexOf(computedStyle[FLEX_DIRECTION]) > -1
      || [HEIGHT, TOP, BOTTOM].indexOf(k) > -1) {
      if(p.u !== AUTO && n.u !== AUTO) {
        diff = calByUnit(p, n, parentComputedStyle[HEIGHT], target.root);
      }
    }
    // 其余都是相对于父width的
    else {
      if(p.u !== AUTO && n.u !== AUTO) {
        diff = calByUnit(p, n, parentComputedStyle[WIDTH], target.root);
      }
    }
    // 兜底NaN非法
    if(diff === 0 || isNaN(diff)) {
      return;
    }
    res.v = diff;
  }
  else if(GRADIENT_HASH.hasOwnProperty(k)) {
    // backgroundImage发生了渐变色和图片的变化，fill发生渐变色和纯色的变化等
    res.v = [];
    let length = Math.min(p.length, n.length);
    for(let i = 0; i < length; i++) {
      let pi = p[i], ni = n[i];
      if(!pi || !ni || pi.u !== ni.u || pi.u === STRING) {
        res.v.push(null);
        continue;
      }
      let u = pi.u;
      pi = pi.v;
      ni = ni.v;
      let temp;
      // 渐变
      if(u === GRADIENT) {
        let r = calDiffGradient(pi, ni, target);
        if(!r) {
          res.v.push(null);
          continue;
        }
        temp = r;
      }
      // 纯色
      else {
        if(equalArr(ni, pi)) {
          res.v.push(null);
          continue;
        }
        temp = [
          ni[0] - pi[0],
          ni[1] - pi[1],
          ni[2] - pi[2],
          ni[3] - pi[3]
        ];
      }
      res.v.push(temp);
    }
  }
  else if(COLOR_HASH.hasOwnProperty(k)) {
    if(n.u !== p.u) {
      return;
    }
    // 特殊增加支持有gradient的先判断，仅color和textStrokeColor支持
    n = n.v;
    p = p.v;
    if(n.u === GRADIENT) {
      let r = calDiffGradient(p, n, target);
      if(!r) {
        return;
      }
      res.v = r;
    }
    else {
      if(equalArr(n, p)) {
        return;
      }
      res.v = [
        n[0] - p[0],
        n[1] - p[1],
        n[2] - p[2],
        n[3] - p[3]
      ];
    }
  }
  else if(GEOM.hasOwnProperty(k)) {
    let tagName = target.tagName;
    if(isNil(p)) {
      return;
    }
    else if(GEOM[k][tagName] && isFunction(GEOM[k][tagName].calDiff)) {
      let fn = GEOM[k][tagName].calDiff;
      if(target.isMulti) {
        let arr = [];
        for(let i = 0, len = Math.min(p.length, n.length); i < len; i++) {
          arr.push(fn(p[i], n[i]));
        }
        return arr;
      }
      else {
        res.v = fn(p, n);
      }
    }
    // 特殊处理multi
    else if(target.isMulti) {
      if(k === 'points' || k === 'controls') {
        if(isNil(n) || !n.length || isNil(p) || !p.length || equalArr(p, n)) {
          return;
        }
        res.v = [];
        for(let i = 0, len = Math.min(p.length, n.length); i < len; i++) {
          let pv = p[i];
          let nv = n[i];
          if(isNil(pv) || !pv.length || isNil(nv) || !nv.length) {
            res.v.push(null);
          }
          else {
            let v2 = [];
            for(let j = 0, len2 = Math.min(pv.length, nv.length); j < len2; j++) {
              let pv2 = pv[j];
              let nv2 = nv[j];
              if(isNil(pv2) || isNil(nv2)) {
                v2.push(null);
              }
              else {
                let v3 = [];
                for(let k = 0, len3 = Math.max(pv2.length, nv2.length); k < len3; k++) {
                  let pv3 = pv2[k];
                  let nv3 = nv2[k];
                  // control由4点变2点
                  if(isNil(pv3) || isNil(nv3)) {
                    v3.push(0);
                  }
                  else {
                    v3.push(nv3 - pv3);
                  }
                }
                v2.push(v3);
              }
            }
            res.v.push(v2);
          }
        }
      }
      else if(k === 'controlA' || k === 'controlB') {
        if(isNil(n) || !n.length || isNil(p) || !p.length || equalArr(p, n)) {
          return;
        }
        res.v = [];
        for(let i = 0, len = Math.min(p.length, n.length); i < len; i++) {
          let pv = p[i];
          let nv = n[i];
          if(isNil(pv) || !pv.length || isNil(nv) || !nv.length) {
            res.v.push(null);
          }
          else {
            res.v.push([
              nv[0] - pv[0],
              nv[1] - pv[1],
            ]);
          }
        }
      }
      else {
        if(n === p || equalArr(n, p) || k === 'edge' || k === 'closure' || k === 'booleanOperations') {
          return;
        }
        let v = [];
        for(let i = 0, len = Math.min(p.length, n.length); i < len; i++) {
          let pv = p[i];
          let nv = n[i];
          if(isNil(pv) || isNil(nv)) {
            v.push(0);
          }
          v.push(nv - pv);
        }
        res.v = v;
      }
    }
    // 非multi特殊处理这几类数组类型数据
    else if(k === 'points' || k === 'controls') {
      if(isNil(n) || !n.length || isNil(p) || !p.length || equalArr(p, n)) {
        return;
      }
      res.v = [];
      for(let i = 0, len = Math.min(p.length, n.length); i < len; i++) {
        let pv = p[i];
        let nv = n[i];
        if(isNil(pv) || !pv.length || isNil(nv) || !nv.length) {
          res.v.push(null);
        }
        else {
          let v2 = [];
          for(let j = 0, len2 = Math.max(pv.length, nv.length); j < len2; j++) {
            let pv2 = pv[j];
            let nv2 = nv[j];
            // control由4点变2点
            if(isNil(pv2) || isNil(nv2)) {
              v2.push(0);
            }
            else {
              v2.push(nv2 - pv2);
            }
          }
          res.v.push(v2);
        }
      }
    }
    else if(k === 'controlA' || k === 'controlB') {
      if(isNil(n) || !n.length || isNil(p) || !p.length || equalArr(p, n)) {
        return;
      }
      res.v = [
        n[0] - p[0],
        n[1] - p[1],
      ];
    }
    // 其它简单数据，除了edge/closure/booleanOperations没有增量
    else {
      if(n === p || k === 'edge' || k === 'closure' || k === 'booleanOperations') {
        return;
      }
      else {
        res.v = n - p;
      }
    }
  }
  // display等不能有增量过程的
  else {
    return;
  }
  return res;
}

// 渐变的差异计算
function calDiffGradient(p, n, target) {
  if(p.k !== n.k || !GRADIENT_TYPE.hasOwnProperty(p.k)) {
    return;
  }
  let pv = p.v;
  let nv = n.v;
  let temp = [[]];
  let eq = equalArr(pv, nv);
  // 渐变值部分是通用的
  for(let i = 0, len = Math.min(pv.length, nv.length); i < len; i++) {
    let a = pv[i];
    let b = nv[i];
    let t = [];
    t.push([
      b[0][0] - a[0][0],
      b[0][1] - a[0][1],
      b[0][2] - a[0][2],
      b[0][3] - a[0][3],
    ]);
    if(a[1] && b[1]) {
      if(a[1].u === b[1].u) {
        t.push(b[1].v - a[1].v);
      }
      else {
        let v = calByUnit(a[1], b[1], target.clientWidth, target.root);
        t.push(v || 0);
      }
    }
    temp[0].push(t);
  }
  if(p.k === 'linear') {
    let isArrP = Array.isArray(p.d);
    let isArrN = Array.isArray(n.d);
    if(isArrN !== isArrP) {
      return;
    }
    if(isArrP) {
      let v = [n.d[0] - p.d[0], n.d[1] - p.d[1], n.d[2] - p.d[2], n.d[3] - p.d[3]];
      if(eq && equalArr(v, [0, 0, 0, 0])) {
        return;
      }
      temp[1] = v;
    }
    else {
      let v = n.d - p.d;
      // 颜色角度都没变化
      if(eq && v === 0) {
        return;
      }
      temp[1] = v;
    }
  }
  else if(p.k === 'radial') {
    let isArrP = Array.isArray(p.z);
    let isArrN = Array.isArray(n.z);
    if(isArrN !== isArrP) {
      return;
    }
    temp[2] = [];
    // sketch中a点到b点特殊格式表达，z是数组忽略p和s
    if(isArrP) {
      // 距离中心外形全等
      if(eq && equalArr(p.z, n.z)) {
        return;
      }
      for(let i = 0; i < 5; i++) {
        let pz = p.z[i];
        // 半径比例省略为1
        if(pz === undefined) {
          pz = 1;
        }
        let nz = n.z[i];
        if(nz === undefined) {
          nz = 1;
        }
        temp[2].push(nz - pz);
      }
    }
    else {
      if(eq && p.z === n.z && p.s === n.s && equalArr(p.p, n.p)) {
        return;
      }
      for(let i = 0; i < 2; i++) {
        let pp = p.p[i];
        let np = n.p[i];
        if(pp.u === np.u) {
          temp[2].push(np.v - pp.v);
        }
        else {
          let v = calByUnit(pp, np, target[i ? 'clientWidth' : 'clientHeight'], target.root);
          temp[2].push(v || 0);
        }
      }
    }
  }
  else if(p.k === 'conic') {
    if(eq && p.d === n.d && equalArr(p.p, n.p)) {
      return;
    }
    temp[1]= n.d - p.d;
    temp[2] = [];
    for(let i = 0; i < 2; i++) {
      let pp = p.p[i];
      let np = n.p[i];
      if(pp[1] === np[1]) {
        temp[2].push(np[0] - pp[0]);
      }
      else {
        let v = calByUnit(pp, np, target[i ? 'clientWidth' : 'clientHeight'], target.root);
        temp[2].push(v || 0);
      }
    }
  }
  return temp;
}

// 计算两帧之间不相同的变化，存入transition，相同的忽略
function calFrame(prev, next, keys, target) {
  keys.forEach(k => {
    let ts = calDiff(prev.style, next.style, k, target);
    // 可以形成过渡的才会产生结果返回
    if(ts) {
      prev.transition.push(ts);
    }
  });
  return next;
}

function binarySearch(i, j, time, frames) {
  while(i < j) {
    let middle = i + ((j - i) >> 1);
    let frame = frames[middle];
    if(frame.time === time) {
      return middle;
    }
    if(frame.time > time) {
      j = Math.max(middle - 1, i);
    }
    else {
      i = Math.min(middle + 1, j);
    }
  }
  return i;
}

function getEasing(ea) {
  let timingFunction;
  if(ea) {
    if((timingFunction = /^\s*steps\s*\(\s*(\d+)(?:\s*,\s*(\w+))?\s*\)/i.exec(ea))) {
      let steps = parseInt(timingFunction[1]);
      let stepsD = timingFunction[2];
      timingFunction = function(percent) {
        // steps有效定义正整数
        if(steps && steps > 0) {
          let per = 1 / steps;
          let n = stepsD === 'start' ? Math.ceil(percent / per) : Math.floor(percent / per);
          return n / steps;
        }
        return percent;
      };
    }
    else {
      timingFunction = easing.getEasing(ea);
    }
  }
  return timingFunction;
}

/**
 * 根据百分比和缓动函数计算中间态样式
 * 当easing定义为steps时，优先计算
 * @param frame 当前帧
 * @param keys 所有样式key
 * @param percent 到下一帧时间的百分比
 * @param target vd
 * @returns {*}
 */
function calIntermediateStyle(frame, keys, percent, target) {
  let { style, transition, timingFunction } = frame;
  if(timingFunction && timingFunction !== linear) {
    percent = timingFunction(percent);
  }
  style = cloneStyle(style, keys);
  for(let i = 0, len = transition.length; i < len; i++) {
    let { k, v } = transition[i];
    let st = style[k];
    // transform特殊处理，只有1个matrix，有可能不存在，需给默认矩阵
    if(k === TRANSFORM) {
      if(!st) {
        st = style[k] = [{ k: MATRIX, v: mx.identity() }];
      }
      for(let i = 0; i < 16; i++) {
        st[0].v[i] += v[i] * percent;
      }
    }
    else if(k === ROTATE_3D) {
      st[0] += v[0] * percent;
      st[1] += v[1] * percent;
      st[2] += v[2] * percent;
      st[3].v += v[3] * percent;
    }
    else if(k === FILTER) {
      // 只有1个样式声明了filter另外一个为空，会造成无样式，需初始化数组并在下面计算出样式存入
      if(!st) {
        st = style[k] = [];
      }
      for(let i = 0, len = v.length; i < len; i++) {
        let item = v[i];
        if(item) {
          let k2 = st[i].k, v2 = st[i].v;
          // 只有dropShadow是多个数组，存放x/y/blur/spread/color
          if(k2 === 'dropShadow') {
            v2[0].v += item[0] * percent;
            v2[1].v += item[1] * percent;
            v2[2].v += item[2] * percent;
            v2[3].v += item[3] * percent;
            let c1 = v2[4], c2 = item[4];
            c1[0] += c2[0] * percent;
            c1[1] += c2[1] * percent;
            c1[2] += c2[2] * percent;
            c1[3] += c2[3] * percent;
          }
          // 其它都是带单位单值
          else {
            v2.v += item * percent;
          }
        }
      }
    }
    else if(k === TRANSFORM_ORIGIN || k === PERSPECTIVE_ORIGIN || RADIUS_HASH.hasOwnProperty(k)) {
      if(v[0] !== 0) {
        st[0].v += v[0] * percent;
      }
      if(v[1] !== 0) {
        st[1].v += v[1] * percent;
      }
    }
    else if(k === BACKGROUND_POSITION_X || k === BACKGROUND_POSITION_Y || k === STROKE_WIDTH) {
      st.forEach((item, i) => {
        if(v[i]) {
          item.v += v[i] * percent;
        }
      });
    }
    else if(k === BOX_SHADOW) {
      for(let i = 0, len = Math.min(st.length, v.length); i < len; i++) {
        if(!v[i]) {
          continue;
        }
        // x/y/blur/spread
        for(let j = 0; j < 4; j++) {
          st[i][j].v += v[i][j] * percent;
        }
        // rgba
        for(let j = 0; j < 4; j++) {
          st[i][4][j] += v[i][4][j] * percent;
        }
      }
    }
    else if(k === BACKGROUND_SIZE) {
      st.forEach((item, i) => {
        let o = v[i];
        if(o) {
          item[0].v += o[0] * percent;
          item[1].v += o[1] * percent;
        }
      });
    }
    else if(k === OPACITY || k === Z_INDEX) {
      style[k] += v * percent;
      // 精度问题可能会超过[0,1]区间
      if(k === OPACITY) {
        if(style[k] < 0) {
          style[k] = 0;
        }
        else if(style[k] > 1) {
          style[k] = 1;
        }
      }
    }
    // 特殊的曲线运动计算，转换为translateXY，出现在最后一定会覆盖原本的translate防重
    else if(k === TRANSLATE_PATH) {
      let t = 1 - percent;
      if(v.length === 8) {
        style[TRANSLATE_X] = {
          v: v[0].v * t * t * t
            + 3 * v[2].v * percent * t * t
            + 3 * v[4].v * percent * percent * t
            + v[6].v * percent * percent * percent,
          u: PX,
        };
        style[TRANSLATE_Y] = {
          v: v[1].v * t * t * t
            + 3 * v[3].v * percent * t * t
            + 3 * v[5].v * percent * percent * t
            + v[7].v * percent * percent * percent,
          u: PX,
        };
      }
      else if(v.length === 6) {
        style[TRANSLATE_X] = {
          v: v[0].v * t * t
            + 2 * v[2].v * percent * t
            + v[4].v * percent * percent,
          u: PX,
        };
        style[TRANSLATE_Y] = {
          v: v[1].v * t * t
            + 3 * v[3].v * percent * t
            + v[5].v * percent * percent,
          u: PX,
        };
      }
    }
    else if(LENGTH_HASH.hasOwnProperty(k) || EXPAND_HASH.hasOwnProperty(k)) {
      if(v) {
        st.v += v * percent;
      }
    }
    else if(GRADIENT_HASH.hasOwnProperty(k)) {
      st.forEach((st2, i) => {
        let v2 = v[i];
        if(!v2) {
          return;
        }
        if(st2.u === GRADIENT && GRADIENT_TYPE.hasOwnProperty(st2.v.k)) {
          st2 = st2.v;
          let [c, d, p, z] = v2;
          for(let i = 0, len = Math.min(st2.v.length, c.length); i < len; i++) {
            let a = st2.v[i];
            let b = c[i];
            a[0][0] += b[0][0] * percent;
            a[0][1] += b[0][1] * percent;
            a[0][2] += b[0][2] * percent;
            a[0][3] += b[0][3] * percent;
            if(a[1] && b[1]) {
              a[1].v += b[1] * percent;
            }
          }
          if(st2.k === 'linear' && st2.d !== undefined && d !== undefined) {
            if(Array.isArray(d)) {
              st2.d[0] += d[0] * percent;
              st2.d[1] += d[1] * percent;
              st2.d[2] += d[2] * percent;
              st2.d[3] += d[3] * percent;
            }
            else {
              st2.d += d * percent;
            }
          }
          if(st2.k === 'radial') {
            if(st2.z !== undefined && z !== undefined) {
              st2.z[0] += z[0] * percent;
              st2.z[1] += z[1] * percent;
              st2.z[2] += z[2] * percent;
              st2.z[3] += z[3] * percent;
              st2.z[4] += z[4] * percent;
            }
            else if(st2.p !== undefined && p !== undefined) {
              st2.p[0].v += p[0] * percent;
              st2.p[1].v += p[1] * percent;
            }
          }
          else if(st2.k === 'conic' && st2.d !== undefined && d !== undefined) {
            st2.d += d * percent;
            st2.p[0][0] += p[0] * percent;
            st2.p[1][0] += p[1] * percent;
          }
        }
        // fill纯色
        else {
          st2 = st2.v;
          st2[0] += v2[0] * percent;
          st2[1] += v2[1] * percent;
          st2[2] += v2[2] * percent;
          st2[3] += v2[3] * percent;
        }
      });
    }
    // color可能超限[0,255]，但浏览器已经做了限制，无需关心
    else if(COLOR_HASH.hasOwnProperty(k)) {
      st = st.v;
      st[0] += v[0] * percent;
      st[1] += v[1] * percent;
      st[2] += v[2] * percent;
      st[3] += v[3] * percent;
    }
    else if(GEOM.hasOwnProperty(k)) {
      let st = style[k];
      let tagName = target.tagName;
      if(GEOM[k][tagName] && isFunction(GEOM[k][tagName].calIncrease)) {
        let fn = GEOM[k][tagName].calIncrease;
        if(target.isMulti) {
          style[k] = st.map((item, i) => {
            return fn(item, v[i], percent);
          });
        }
        else {
          style[k] = fn(st, v, percent);
        }
      }
      else if(target.isMulti) {
        if(k === 'points' || k === 'controls') {
          for(let i = 0, len = Math.min(st.length, v.length); i < len; i++) {
            let o = st[i];
            let n = v[i];
            if(!isNil(o) && !isNil(n)) {
              for(let j = 0, len2 = Math.min(o.length, n.length); j < len2; j++) {
                let o2 = o[j];
                let n2 = n[j];
                if(!isNil(o2) && !isNil(n2)) {
                  for(let k = 0, len3 = Math.min(o2.length, n2.length); k < len3; k++) {
                    if(!isNil(o2[k]) && !isNil(n2[k])) {
                      o2[k] += n2[k] * percent;
                    }
                  }
                }
              }
            }
          }
        }
        else if(k === 'controlA' || k === 'controlB') {
          v.forEach((item, i) => {
            let st2 = st[i];
            if(!isNil(item) && !isNil(st2)) {
              for(let i = 0, len = Math.min(st2.length, item.length); i < len; i++) {
                let o = st2[i];
                let n = item[i];
                if(!isNil(o) && !isNil(n)) {
                  st2[i] += n * percent;
                }
              }
            }
          });
        }
        else {
          v.forEach((item, i) => {
            if(!isNil(item) && !isNil(st[i])) {
              st[i] += item * percent;
            }
          });
        }
      }
      else {
        if(k === 'points' || k === 'controls') {
          for(let i = 0, len = Math.min(st.length, v.length); i < len; i++) {
            let o = st[i];
            let n = v[i];
            if(!isNil(o) && !isNil(n)) {
              for(let j = 0, len2 = Math.min(o.length, n.length); j < len2; j++) {
                if(!isNil(o[j]) && !isNil(n[j])) {
                  o[j] += n[j] * percent;
                }
              }
            }
          }
        }
        else if(k === 'controlA' || k === 'controlB') {
          if(!isNil(st[0]) && !isNil(v[0])) {
            st[0] += v[0] * percent;
          }
          if(!isNil(st[1]) && !isNil(v[1])) {
            st[1] += v[1] * percent;
          }
        }
        else {
          if(!isNil(st) && !isNil(v)) {
            style[k] += v * percent;
          }
        }
      }
    }
  }
  return style;
}

function gotoOverload(options, cb) {
  if(isFunction(options)) {
    cb = options;
    options = {};
  }
  return [options || {}, cb];
}

function frameCb(self, diff, isDelay) {
  self.emit(Event.FRAME, diff, isDelay);
  if(self.__firstPlay) {
    self.__firstPlay = false;
    self.emit(Event.PLAY);
  }
  let cb = self.__playCb;
  if(isFunction(cb)) {
    cb.call(self, diff, isDelay);
    // 清理要检查，gotoAndStop()这种cb回调中直接再次调用goto的话cb会不一致不能删除
    if(self.__playCb === cb) {
      self.__playCb = null;
    }
  }
}

let uuid = 0;

class Animation extends Event {
  constructor(target, list, options) {
    super();
    this.__id = uuid++;
    list = clone(list || []);
    if(Array.isArray(list)) {
      list = list.filter(item => item && isObject(item));
    }
    // 动画过程另外一种形式，object描述k-v形式
    else if(list && isObject(list)) {
      let nl = [];
      Object.keys(list).forEach(k => {
        let v = list[k];
        if(Array.isArray(v)) {
          for(let i = 0, len = v.length; i < len; i++) {
            let o = nl[i] = nl[i] || {
              offset: i / (len - 1),
            };
            o[k] = v[i];
          }
        }
      });
      list = nl;
    }
    else {
      list = [];
    }
    if(isNumber(options)) {
      this.__options = {
        duration: options,
      };
      options = this.__options;
    }
    let op = this.__options = options || {
      duration: 0,
    };
    this.__begin = true;
    this.__playState = 'idle';
    this.__target = target;
    this.__root = target.root;
    this.__style = {};
    this.__firstPlay = true;
    this.__firstEnter = true;
    let iterations = this.iterations = op.iterations;
    let duration = this.duration = op.duration;
    let [frames, framesR, keys, originStyle] = this.__init(list, iterations, duration, op.easing, target);
    this.__frames = frames;
    this.__framesR = framesR;
    this.__keys = keys;
    this.__originStyle = originStyle;
    this.__outBeginDelay = false;
    this.__playCount = 0;
    this.__firstEnter = true;
    let fps = parseInt(op.fps) || 0;
    if(fps <= 0) {
      fps = 60;
    }
    this.fps = fps;
    this.spfLimit = op.spfLimit;
    this.delay = op.delay;
    this.endDelay = op.endDelay;
    this.playbackRate = op.playbackRate;
    this.fill = op.fill;
    this.iterations = op.iterations;
    this.direction = op.direction;
    this.easing = op.easing;
    this.__currentFrames = {
      reverse: true,
      'alternate-reverse': true,
    }.hasOwnProperty(op.direction) ? framesR : frames;
    // 时间戳
    this.__timestamp = frame.__now;
  }

  __init(list, iterations, duration, easing, target) {
    if(list.length < 1) {
      return [[], [], [], {}];
    }
    // 过滤时间非法的，过滤后续offset<=前面的
    let offset = -1;
    for(let i = 0, len = list.length; i < len; i++) {
      let current = list[i];
      if(current.hasOwnProperty('offset')) {
        current.offset = parseFloat(current.offset) || 0;
        current.offset = Math.max(0, current.offset);
        current.offset = Math.min(1, current.offset);
        // 超过区间[0,1]
        if(isNaN(current.offset) || current.offset < 0 || current.offset > 1) {
          list.splice(i, 1);
          i--;
          len--;
          continue;
        }
        // <=前面的
        else if(current.offset <= offset) {
          list.splice(i, 1);
          i--;
          len--;
          continue;
        }
      }
      // 缩写处理
      Object.keys(current).forEach(k => {
        if(abbr.hasOwnProperty(k)) {
          abbr.toFull(current, k);
        }
      });
      // 检查key合法性
      Object.keys(current).forEach(k => {
        if(k !== 'easing' && k !== 'offset' && !change.isValid(target.tagName, k)) {
          delete current[k];
        }
      });
    }
    // 只有1帧复制出来变成2帧方便运行
    if(list.length === 1) {
      list[0] = clone(list[0]);
      if(list[0].offset === 1) {
        list.unshift({
          offset: 0,
        });
      }
      else {
        let copy = clone(list[0]);
        copy.offset = 1;
        list.push(copy);
      }
    }
    // 强制clone防止同引用
    else {
      list.forEach((item, i) => {
        list[i] = clone(item);
      });
    }
    // 首尾时间偏移强制为[0, 1]，不是的话前后加空帧
    let first = list[0];
    if(first.hasOwnProperty('offset') && first.offset > 0) {
      first = {
        offset: 0,
      };
      list.unshift(first);
    }
    else {
      first.offset = 0;
    }
    let last = list[list.length - 1];
    if(last.hasOwnProperty('offset') && last.offset < 1) {
      last = {
        offset: 1,
      };
      list.push(last);
    }
    else {
      last.offset = 1;
    }
    // 计算没有设置offset的时间
    for(let i = 1, len = list.length; i < len; i++) {
      let start = list[i];
      // 从i=1开始offset一定>0，找到下一个有offset的，均分中间无声明的
      if(!start.hasOwnProperty('offset')) {
        let end;
        let j = i + 1;
        for(; j < len; j++) {
          end = list[j];
          if(end.hasOwnProperty('offset')) {
            break;
          }
        }
        let num = j - i + 1;
        start = list[i - 1];
        let per = (end.offset - start.offset) / num;
        for(let k = i; k < j; k++) {
          let item = list[k];
          item.offset = start.offset + per * (k + 1 - i);
        }
        i = j;
      }
    }
    let frames = [];
    // 换算每一关键帧样式标准化
    list.forEach(item => {
      frames.push(framing(item, duration, easing));
    });
    // 为方便两帧之间计算变化，强制统一所有帧的css属性相同，没有写的为节点的当前样式currentStyle
    let keys = unify(frames, target);
    inherit(frames, keys, target);
    let framesR = clone(frames).reverse();
    // 存储原本样式以便恢复用
    let { style, props } = target;
    let originStyle = {};
    keys.forEach(k => {
      if(isGeom(target.tagName, k)) {
        originStyle[k] = props[k];
      }
      originStyle[k] = style[k];
    });
    // 再计算两帧之间的变化，存入transition属性
    let length = frames.length;
    let prev = frames[0];
    for(let i = 1; i < length; i++) {
      let next = frames[i];
      prev = calFrame(prev, next, keys, target);
    }
    // 反向存储帧的倒排结果
    framesR.forEach(item => {
      item.time = duration - item.time;
      item.transition = [];
    });
    prev = framesR[0];
    for(let i = 1; i < length; i++) {
      let next = framesR[i];
      prev = calFrame(prev, next, keys, target);
    }
    return [frames, framesR, keys, originStyle];
  }

  __clean(isFinish) {
    this.__cancelTask();
    this.__nextTime = 0;
    let restore;
    let style = this.__style;
    let keys = this.__keys;
    let target = this.__target;
    if(isFinish) {
      // gotoAndStop到一个很大的时间的话，也需要防止超过
      this.__currentTime = this.__delay + this.__duration * this.__iterations + this.__endDelay;
      if(this.__playState === 'finish') {
        return;
      }
      this.__playState = 'finish';
      // cancel需要清除finish根据情况保留
      if(!this.__stayEnd) {
        this.__style = {};
        restore = true;
      }
    }
    else {
      this.__playCount = this.__currentTime = 0;
      if(this.__playState === 'idle') {
        return;
      }
      this.__playState = 'idle';
      this.__style = {};
      restore = true;
    }
    // 动画取消结束不停留在最后一帧需要还原target原本的样式，需要对比目前是否是由本动画赋值的
    if(restore) {
      keys.forEach(k => {
        if(GEOM.hasOwnProperty(k)) {
          if(target.__currentProps[k] === style[k]) {
            target.__currentProps[k] = target.props[k];
          }
        }
        else {
          if(target.__currentStyle[k] === style[k]) {
            target.__currentStyle[k] = target.style[k];
          }
        }
      });
    }
  }

  play(cb) {
    let isDestroyed = this.__isDestroyed;
    let duration = this.__duration;
    let playState = this.__playState;
    let frames = this.__frames;
    if(isDestroyed || duration <= 0 || frames.length < 1) {
      return this;
    }
    if(playState === 'running') {
      return this;
    }
    this.__cancelTask();
    this.__playCb = cb;
    this.__playState = 'running';
    // 每次play调用标识第一次运行，需响应play事件和回调
    this.__firstPlay = true;
    // 防止finish/cancel事件重复触发，每次播放重置
    this.__hasFin = false;
    this.__hasCancel = false;
    // 只有第一次调用会进初始化，另外finish/cancel视为销毁也会重新初始化
    if(!this.__enterFrame) {
      this.__enterFrame = true;
      let framesR = this.__framesR;
      let direction = this.__direction;
      // 初始化根据方向确定帧序列
      this.__currentFrames = {
        reverse: true,
        'alternate-reverse': true,
      }.hasOwnProperty(direction) ? framesR : frames;
      this.__currentTime = this.__nextTime = this.__fpsTime = 0;
    }
    // 添加每帧回调且立刻执行，本次执行调用refreshTask也是下一帧再渲染，frame的每帧都是下一帧
    // frame.offFrame(this);
    frame.onFrame(this);
    this.__startTime = frame.__now;
    this.__end = false;
    return this;
  }

  __before(diff) {
    this.__timestamp = frame.__now;
    let target = this.__target;
    let fps = this.__fps;
    let currentFrames = this.__currentFrames;
    let iterations = this.__iterations;
    let stayBegin = this.__stayBegin;
    let stayEnd = this.__stayEnd;
    let delay = this.__delay;
    let root = this.__root;
    let duration = this.__duration;
    let endDelay = this.__endDelay;
    let length = currentFrames.length;
    let playbackRate = this.__playbackRate;
    let spfLimit = this.__spfLimit;
    this.__currentTime = this.__nextTime;
    // 定帧限制每帧时间间隔最大为spf
    if(spfLimit) {
      if(spfLimit === true) {
        diff = Math.min(diff, 1000 / fps);
      }
      else if(spfLimit > 0) {
        diff = Math.min(diff, spfLimit);
      }
    }
    // 播放时间累加，并且考虑播放速度加成
    if(playbackRate !== 1 && playbackRate > 0) {
      diff *= playbackRate;
    }
    // 用本帧和上帧时间差，计算累加运行时间currentTime，以便定位当前应该处于哪个时刻
    this.__nextTime += diff;
    let currentTime = this.__currentTime;
    // 增加的fps功能，当<60时计算跳帧，每帧运行依旧累加时间，达到fps时重置，第一帧强制不跳
    if(!this.__firstEnter && fps < 60) {
      diff = this.__fpsTime += diff;
      if(diff < 1000 / fps) {
        this.__inFps = true;
        return;
      }
      this.__fpsTime = 0;
    }
    this.__firstEnter = false;
    // delay仅第一次生效等待
    if(currentTime < delay) {
      if(stayBegin) {
        let currentFrame = this.__currentFrame = currentFrames[0];
        let current = currentFrame.style;
        genBeforeRefresh(this, current, this.__keys, root, target);
      }
      this.__begin = false; // 默认是true，delay置false防触发
      // 即便不刷新，依旧执行帧回调，同时标明让后续第一帧响应begin
      this.__outBeginDelay = true;
      this.__isDelay = true;
      return;
    }
    // 减去delay，计算在哪一帧
    currentTime -= delay;
    if(this.__outBeginDelay) {
      this.__outBeginDelay = false;
      this.__begin = true;
    }
    // 超过duration非尾轮需处理回到开头，触发新一轮动画事件，这里可能时间间隔非常大直接跳过几轮
    let playCount = Math.min(iterations - 1, Math.floor(currentTime / duration));
    currentTime -= duration * playCount;
    // 如果发生轮换，需重新确定正反向
    if(this.__playCount < playCount) {
      this.__begin = true;
      this.__playCount = playCount;
      let direction = this.__direction;
      let frames = this.__frames;
      let framesR = this.__framesR;
      // 有正反向播放需要重设帧序列
      if(direction === 'alternate' || direction === 'alternate-reverse') {
        let isEven = playCount % 2 === 0;
        if(direction === 'alternate') {
          currentFrames = this.__currentFrames = isEven ? frames : framesR;
        }
        else {
          currentFrames = this.__currentFrames = isEven ? framesR : frames;
        }
      }
    }
    let isLastCount = playCount >= iterations - 1;
    // 只有2帧可优化，否则2分查找当前帧
    let i, frameTime;
    if(length === 2) {
      i = currentTime < duration ? 0 : 1;
      frameTime = duration;
    }
    else {
      i = binarySearch(0, length - 1, currentTime, currentFrames);
      frameTime = currentFrames[i].time;
    }
    // 最后一帧结束动画，仅最后一轮才会进入，需处理endDelay
    let isLastFrame = isLastCount && i === length - 1;
    let percent = 0;
    if(isLastFrame) {
      // 无需任何处理
    }
    // 否则根据目前到下一帧的时间差，计算百分比，再反馈到变化数值上
    else if(length === 2) {
      percent = currentTime / duration;
    }
    else {
      let total = currentFrames[i + 1].time - frameTime;
      percent = (currentTime - frameTime) / total;
    }
    let inEndDelay, currentFrame = currentFrames[i], current;
    this.__currentFrame = currentFrame;
    /** 这里要考虑全几种场景：
     * 1. 单次播放无endDelay且fill不停留（有/无差异，下同）
     * 2. 单次播放无endDelay且fill停留
     * 3. 单次播放有endDelay且fill不停留
     * 4. 单次播放有endDelay且fill停留
     * 5. 多次播放无endDelay且fill不停留（尾次/非尾次，下同）
     * 6. 多次播放无endDelay且fill停留
     * 7. 多次播放有endDelay且fill不停留
     * 8. 多次播放有endDelay且fill停留
     */
    let needClean;
    if(isLastFrame) {
      inEndDelay = currentTime < duration + endDelay;
      // 停留对比最后一帧，endDelay可能会多次进入这里，第二次进入样式相等不再重绘
      if(stayEnd) {
        current = cloneStyle(currentFrame.style, this.__keys);
      }
      // 不停留或超过endDelay则计算还原，有endDelay且fill模式不停留会再次进入这里
      else {
        current = cloneStyle(this.__originStyle, this.__keys);
      }
      // 进入endDelay或结束阶段触发end事件，注意只触发一次，防重在触发的地方做
      this.__nextEnd = true;
      if(!inEndDelay) {
        this.__playCount++;
        this.__finished = true;
        frame.offFrame(this);
        needClean = true;
        this.__nextTime = 0;
      }
    }
    else {
      current = calIntermediateStyle(currentFrame, this.__keys, percent, target);
    }
    // 无论两帧之间是否有变化，都生成计算结果赋给style，去重在root做
    genBeforeRefresh(this, current, this.__keys, root, target);
    if(needClean) {
      let playCb = this.__playCb;
      this.__clean(true);
      // 丑陋的做法，防止gotoAndStop()这样的cb被clean()掉
      if(playCb) {
        this.__playCb = playCb;
      }
    }
  }

  __after(diff) {
    this.__assigning = false;
    if(this.__inFps) {
      this.__inFps = false;
      return;
    }
    frameCb(this, diff, this.__isDelay);
    this.__isDelay = false;
    if(this.__begin) {
      this.__begin = false;
      this.emit(Event.BEGIN, this.__playCount);
    }
    // end事件只触发一次，末轮进入endDelay或直接结束时
    if(this.__nextEnd && !this.__end) {
      this.__end = true;
      this.emit(Event.END, this.__playCount - 1);
    }
    if(this.__finished) {
      this.__begin = this.__end = this.__isDelay = this.__finished
        = this.__inFps = this.__enterFrame = false;
      this.__playState = 'finished';
      this.emit(Event.FINISH);
    }
  }

  pause(silence) {
    let isDestroyed = this.__isDestroyed;
    let duration = this.__duration;
    let { pending } = this;
    if(isDestroyed || duration <= 0 || pending) {
      return this;
    }
    this.__playState = 'paused';
    this.__cancelTask();
    if(!silence) {
      this.emit(Event.PAUSE);
    }
    return this;
  }

  resume(cb) {
    let isDestroyed = this.__isDestroyed;
    let duration = this.__duration;
    let playState = this.__playState;
    if(isDestroyed || duration <= 0 || playState !== 'paused') {
      return this;
    }
    return this.play(cb);
  }

  finish(cb) {
    let self = this;
    let isDestroyed = self.__isDestroyed;
    let duration = self.__duration;
    let playState = self.__playState;
    let frames = self.__frames;
    if(isDestroyed || duration <= 0 || frames.length < 1 || playState === 'finished' || playState === 'idle') {
      return self;
    }
    // 先清除所有回调任务，多次调用finish也会清除只留最后一次
    self.__cancelTask();
    let root = self.__root;
    let originStyle = self.__originStyle;
    if(root) {
      let current;
      // 停留在最后一帧
      if(this.__stayEnd) {
        this.__currentFrame = frames[frames.length - 1];
        current = frames[frames.length - 1].style;
      }
      else {
        current = originStyle;
      }
      root.addRefreshTask({
        __before() {
          self.__assigning = true;
          genBeforeRefresh(self, current, self.__keys, root, self.__target);
          self.__clean(true);
        },
        __after(diff) {
          if(!self.__hasFin) {
            self.__hasFin = true;
            self.__assigning = false;
            frameCb(self, diff, false);
            self.__begin = self.__end = self.__isDelay = self.__finished
              = self.__inFps = self.__enterFrame = false;
            self.emit(Event.FINISH);
          }
          if(isFunction(cb)) {
            cb.call(self, diff);
          }
        },
      });
    }
    return self;
  }

  cancel(cb) {
    let self = this;
    let isDestroyed = self.__isDestroyed;
    let duration = self.__duration;
    let playState = self.__playState;
    let frames = self.__frames;
    if(isDestroyed || duration <= 0 || playState === 'idle' || frames.length < 1) {
      return self;
    }
    self.__cancelTask();
    let root = self.__root;
    let originStyle = self.__originStyle;
    if(root) {
      root.addRefreshTask({
        __before() {
          self.__assigning = true;
          genBeforeRefresh(self, originStyle, self.__keys, root, self.__target);
          self.__clean();
        },
        __after(diff) {
          if(!self.__hasCancel) {
            self.__hasCancel = true;
            self.__assigning = false;
            frameCb(self, diff, false);
            self.__begin = self.__end = self.__isDelay = self.__finished
              = self.__inFps = self.__enterFrame = false;
            self.emit(Event.CANCEL);
          }
          if(isFunction(cb)) {
            cb.call(self, diff);
          }
        },
      });
    }
    return self;
  }

  gotoAndPlay(v, options, cb) {
    let isDestroyed = this.__isDestroyed;
    let duration = this.__duration;
    let frames = this.__frames;
    let delay = this.__delay;
    let endDelay = this.__endDelay;
    if(isDestroyed || duration <= 0 || frames.length < 1) {
      return this;
    }
    [options, cb] = gotoOverload(options, cb);
    // 计算出时间点直接累加播放
    this.__goto(v, options.isFrame, options.excludeDelay);
    if(v > duration + delay + endDelay) {
      return this.finish(cb);
    }
    return this.play(cb);
  }

  gotoAndStop(v, options, cb) {
    let isDestroyed = this.__isDestroyed;
    let duration = this.__duration;
    let frames = this.__frames;
    let delay = this.__delay;
    let endDelay = this.__endDelay;
    if(isDestroyed || duration <= 0 || frames.length < 1) {
      return this;
    }
    [options, cb] = gotoOverload(options, cb);
    v = this.__goto(v, options.isFrame, options.excludeDelay);
    if(v > duration + delay + endDelay) {
      return this.finish(cb);
    }
    // 先play一帧，回调里模拟暂停
    return this.play(diff => {
      this.__playState = 'paused';
      this.__cancelTask();
      if(isFunction(cb)) {
        cb.call(this, diff);
      }
    });
  }

  // 同步赋予，用在extendAnimate
  assignCurrentStyle() {
    let style = this.__style;
    let target = this.__target;
    let keys = this.__keys;
    keys.forEach(i => {
      if(style.hasOwnProperty(i)) {
        let v = style[i];
        // geom的属性变化
        if(GEOM.hasOwnProperty(i)) {
          target.currentProps[i] = v;
        }
        // 样式
        else {
          // 将动画样式直接赋给currentStyle
          target.currentStyle[i] = v;
        }
      }
    });
  }

  // 返回不包含delay且去除多轮的时间
  __goto(v, isFrame, excludeDelay) {
    let iterations = this.__iterations;
    let duration = this.__duration;
    this.__playState = 'paused';
    // this.__cancelTask(); // 应该不需要，gotoAndXxx都会调用play()，里面有
    if(isNaN(v) || v < 0) {
      throw new Error('Param of gotoAnd(Play/Stop) is illegal: ' + v);
    }
    if(isFrame) {
      v = (v - 1) / this.spf;
    }
    if(excludeDelay) {
      v += this.__delay;
    }
    // 在时间范围内设置好时间，复用play直接跳到播放点
    this.__nextTime = v;
    v -= this.__delay;
    // 超过时间长度需要累加次数，这里可以超过iterations，因为设定也许会非常大
    let playCount = 0;
    while(v >= duration && playCount < iterations - 1) {
      playCount++;
      v -= duration;
    }
    this.__playCount = playCount;
    // 防止play()重置时间和当前帧组，提前计算好
    this.__enterFrame = true;
    let frames = this.__frames;
    let framesR = this.__framesR;
    let direction = this.__direction;
    if({
      alternate: true,
      'alternate-reverse': true,
    }.hasOwnProperty(direction)) {
      let isEven = playCount % 2 === 0;
      if(direction === 'alternate') {
        this.__currentFrames = isEven ? frames : framesR;
      }
      else {
        this.__currentFrames = isEven ? framesR : frames;
      }
    }
    return v;
  }

  addControl() {
    let root = this.root;
    if(!root) {
      return;
    }
    let ac = root.animateController;
    if(ac) {
      ac.add(this);
    }
  }

  removeControl() {
    let root = this.root;
    if(!root) {
      return;
    }
    let ac = root.animateController;
    if(ac) {
      ac.remove(this);
    }
  }

  // __stayBegin() {
  //   return {
  //     backwards: true,
  //     both: true,
  //   }.hasOwnProperty(this.fill);
  // }
  //
  // __stayEnd() {
  //   return {
  //     forwards: true,
  //     both: true,
  //   }.hasOwnProperty(this.fill);
  // }

  __setTarget(target) {
    this.__target = target;
  }

  __cancelTask() {
    frame.offFrame(this);
    this.__playCb = null;
  }

  __destroy(sync) {
    let self = this;
    if(this.__isDestroyed) {
      return;
    }
    self.removeControl();
    // clean异步执行，因为里面的样式还原需要等到下一帧，否则同步执行清除后，紧接着的新同步动画获取不到currentStyle
    if(sync) {
      self.__clean();
      this.__target = null;
    }
    else {
      frame.nextFrame({
        __before() {
          self.__clean();
          this.__target = null;
        },
      });
    }
    this.__startTime = 0;
    this.__isDestroyed = true;
  }

  __checkModify() {
    if(this.__playState !== 'idle' && this.__playState !== 'finished') {
      inject.warn('Modification will not come into effect when animation is running');
    }
  }

  get id() {
    return this.__id;
  }

  get target() {
    return this.__target;
  }

  get root() {
    return this.__root;
  }

  get keys() {
    return this.__keys;
  }

  get style() {
    return this.__style;
  }

  get options() {
    return this.__options;
  }

  get duration() {
    return this.__duration;
  }

  set duration(v) {
    v = Math.max(0, parseFloat(v) || 0);
    if(this.__duration !== v) {
      this.__duration = v;
      this.__checkModify();
    }
    return v;
  }

  get delay() {
    return this.__delay;
  }

  set delay(v) {
    v = Math.max(0, parseFloat(v) || 0);
    if(this.__delay !== v) {
      this.__delay = v;
      this.__checkModify();
    }
    return v;
  }

  get endDelay() {
    return this.__endDelay;
  }

  set endDelay(v) {
    v = Math.max(0, parseFloat(v) || 0);
    if(this.__endDelay !== v) {
      this.__endDelay = v;
      this.__checkModify();
    }
    return v;
  }

  get fps() {
    return this.__fps;
  }

  set fps(v) {
    v = parseInt(v) || 60;
    if(this.__fps !== v) {
      if(v <= 0) {
        v = 60;
      }
      this.__fps = v;
    }
    return v;
  }

  get spf() {
    return 1 / this.fps;
  }

  get iterations() {
    return this.__iterations;
  }

  set iterations(v) {
    if(v === Infinity || util.isString(v) && v.toLowerCase() === 'infinity') {
      v = Infinity;
    }
    else {
      v = parseInt(v);
      if(isNaN(v) || v < 0) {
        v = 1;
      }
    }
    if(this.__iterations !== v) {
      this.__iterations = v;
    }
    return v;
  }

  get fill() {
    return this.__fill;
  }

  set fill(v) {
    v = v || 'none';
    if(this.__fill !== v) {
      this.__fill = v;
      this.__checkModify();
    }
    this.__stayBegin = {
      backwards: true,
      both: true,
    }.hasOwnProperty(v);
    this.__stayEnd = {
      forwards: true,
      both: true,
    }.hasOwnProperty(v);
    return v;
  }

  get direction() {
    return this.__direction;
  }

  set direction(v) {
    v = v || 'normal';
    if(this.__direction !== v) {
      this.__direction = v;
      this.__checkModify();
    }
    return v;
  }

  get frames() {
    return this.__frames;
  }

  get framesR() {
    return this.__framesR;
  }

  get playbackRate() {
    return this.__playbackRate;
  }

  set playbackRate(v) {
    v = parseFloat(v) || 1;
    if(v <= 0) {
      v = 1;
    }
    if(this.__playbackRate !== v) {
      this.__playbackRate = v;
    }
    return v;
  }

  get easing() {
    return this.__easing;
  }

  set easing(v) {
    this.__easing = v;
  }

  get startTime() {
    return this.__startTime;
  }

  get currentTime() {
    return this.__currentTime;
  }

  set currentTime(v) {
    v = Math.max(0, parseFloat(v) || 0);
    if(this.__currentTime !== v) {
      this.__currentTime = v;
      this.__nextTime = v;
    }
    return v;
  }

  get nextTime() {
    return this.__nextTime;
  }

  set nextTime(v) {
    v = Math.max(0, parseFloat(v) || 0);
    if(this.__nextTime !== v) {
      this.__nextTime = v;
    }
    return v;
  }

  get timestamp() {
    return this.__timestamp;
  }

  get pending() {
    return this.__playState !== 'running';
  }

  get finished() {
    return this.__playState === 'finished';
  }

  get playState() {
    return this.__playState;
  }

  get playCount() {
    return this.__playCount;
  }

  set playCount(v) {
    v = Math.max(0, parseInt(v) || 0);
    if(this.__playCount !== v) {
      this.__playCount = v;
    }
    return v;
  }

  get isDestroyed() {
    return this.__isDestroyed;
  }

  get animating() {
    let playState = this.__playState;
    if(playState === 'idle') {
      return false;
    }
    return playState !== 'finished' || this.__stayEnd || this.__stayBegin;
  }

  get spfLimit() {
    return this.__spfLimit;
  }

  set spfLimit(v) {
    if(util.isNumber(v) || /^\d/.test(v)) {
      v = Math.max(0, parseInt(v) || 0);
    }
    else {
      v = !!v;
    }
    if(this.__spfLimit !== v) {
      this.__spfLimit = v;
    }
    return v;
  }

  get assigning() {
    return this.__assigning;
  }
}

export default Animation;
